import axios from 'axios';

//const base = 'http://localhost:8000/api'
const base = 'https://appmeubarbeiro.com.br/public/api'
//const base = 'https://meubarbeiroapi.herokuapp.com/public/api'

const Axios = axios.create({
  baseURL: base,
  headers: {
    'Content-Type': 'application/json',
  }
});

Axios.interceptors.request.use(
  function (config) {
    config.headers.token = localStorage.token ?? null;
    return config;
  },
  function (error) {
    return Promise.reject(error);
  }
);

Axios.interceptors.response.use(
  function (response) {
    return response;
  },
  function (error) {
    if (401 === error.response.status) {
      const path = window.location.pathname
      //localStorage.lastUrl = path
      //path = path.replace('/', '');
      //localStorage.routePath = path;
      let destiny = '/autenticacao/login'
      if (path.includes('portal')) destiny = '/barbeiro/autenticacao'
      else if (path.includes('moderadores')) destiny = '/moderadores/login'
      window.console.log(destiny)
      //window.location.href = destiny;
    } else return Promise.reject(error);
  }
);

export default Axios;
